import React, {Component} from 'react';
import {withRouter} from "react-router-dom";
import {connect} from "react-redux";
import PropTypes from "prop-types";
import {
    getAppStatus,
    getBusinessCategories, getBusinessOptionFromUrl, setBusinessCategoryId, setCurrentBusinessOption,
    setCurrentTipCategory,
    setSellGoods
} from "../../actions/appStatusAction";
import { saveBusinessOptionFormRequest} from "../../actions/businessActions";
import {addFlashMessage} from "../../actions/flashMessageAction";

class SocialMediaRegistration extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isFacebookClicked: false,
            isTwitterClicked: false,
            isLinkedinClicked: false,
            isInstagramClicked: false
        }
    }

    onClickNext(e) {
        e.preventDefault();
        this.props.getAppStatus();
        const appStatus = this.props.appStatus;
        this.props.getBusinessOptionFromUrl(appStatus.currentBusinessOption.links.next);
    }

    onClickSocialIcon(e, icon) {
        e.preventDefault();

        switch(icon) {
            case 'twitter':
                this.setState({isTwitterClicked: true});
                break;
            case 'facebook':
                this.setState({isFacebookClicked: true});
                break;
            case 'linkedin':
                this.setState({isLinkedinClicked: true});
                break;
            case 'instagram':
                this.setState({isInstagramClicked: true});
                break;
            default:
        }
    }

    onClickOption(e, option) {
        e.preventDefault();

        this.props.saveBusinessOptionFormRequest({
                business_option_id: this.props.appStatus.currentBusinessOption.id,
                business_meta: {
                    social_media_registration: option
                }
            }
        ).then(
            (response) => {
                this.props.addFlashMessage({
                    type: "success",
                    text: "Saved successfully!"
                });
                console.log('financing option: response', response.data.business_option);
                this.props.setCurrentBusinessOption(response.data.business_option);
            }
        );
    }

    onClickUpdateStatus(e, status) {
        e.preventDefault();

        this.props.saveBusinessOptionFormRequest({
            business_option_id: this.props.appStatus.currentBusinessOption.id,
            business_option_status: status
        }).then(
            (response) => {
                this.props.addFlashMessage({
                    type: "success",
                    text: "Saved successfully!"
                });
                console.log('financing option: response', response.data.business_option);
                this.props.setCurrentBusinessOption(response.data.business_option);
            }
        );
    }

    render() {
        const { appStatus } = this.props;
        const currentBusinessOption = appStatus.currentBusinessOption;
        const currentBusinessMeta = currentBusinessOption.business_meta;

        return (
            <div>
                {
                    !(this.state.isFacebookClicked || this.state.isTwitterClicked || this.state.isLinkedinClicked || this.state.isInstagramClicked)

                    &&

                    <ul className="apps-social-media">
                        <li><a href="" onClick={(e) => this.onClickSocialIcon(e, 'twitter')}><img src={`${process.env.PUBLIC_URL}/assets/images/social/img_5.png`} alt="" /></a></li>
                        <li><a href="" onClick={(e) => this.onClickSocialIcon(e, 'facebook')}><img src={`${process.env.PUBLIC_URL}/assets/images/social/img_2.png`} alt="" /></a></li>
                        <li><a href="" onClick={(e) => this.onClickSocialIcon(e, 'linkedin')}><img src={`${process.env.PUBLIC_URL}/assets/images/social/img_3.png`} alt="" /></a></li>
                        <li><a href="" onClick={(e) => this.onClickSocialIcon(e, 'instagram')}><img src={`${process.env.PUBLIC_URL}/assets/images/social/img_4.png`} alt="" /></a></li>
                    </ul>
                }
                {
                    this.state.isTwitterClicked &&
                    <ul className="apps-social-media">
                        <li>
                            <a href="" onClick={(e) => this.onClickSocialIcon(e, 'twitter')}><img src={`${process.env.PUBLIC_URL}/assets/images/social/img_5.png`} alt="" /></a>
                            <input type="text" />
                        </li>
                    </ul>
                }

                {
                    this.state.isFacebookClicked &&
                    <ul className="apps-social-media">
                        <li>
                            <a href="" onClick={(e) => this.onClickSocialIcon(e, 'facebook')}><img src={`${process.env.PUBLIC_URL}/assets/images/social/img_2.png`} alt="" /></a>
                            <input type="text" />
                        </li>
                    </ul>
                }

                {
                    this.state.isLinkedinClicked &&
                    <ul className="apps-social-media">
                        <li>
                            <a href="" onClick={(e) => this.onClickSocialIcon(e, 'linkedin')}><img src={`${process.env.PUBLIC_URL}/assets/images/social/img_3.png`} alt="" /></a>
                            <input type="text" />
                        </li>
                    </ul>
                }

                {
                    this.state.isInstagramClicked &&
                    <ul className="apps-social-media">
                        <li>
                            <a href="" onClick={(e) => this.onClickSocialIcon(e, 'instagram')}><img src={`${process.env.PUBLIC_URL}/assets/images/social/img_4.png`} alt="" /></a>
                            <input type="text" />
                        </li>
                    </ul>
                }

                <ul className="alert-btns">
                    <li><a
                        className={ currentBusinessMeta.social_media_registration == 'option 1' ? 'active' : '' }
                        href="" onClick={(e) => this.onClickOption(e, 'option 1')}>Option 1</a></li>
                    <li><a
                        className={ currentBusinessMeta.social_media_registration == 'option 2' ? 'active' : '' }
                        href="" onClick={(e) => this.onClickOption(e, 'option 2')}>Option 2</a></li>
                </ul>
                <ul className="alert-f-links">
                    <li><a
                        className={currentBusinessOption.business_business_option_status == 'skipped' ? 'active' : ''}
                        href="" onClick={(e) => this.onClickUpdateStatus(e, 'skipped')}>Not now</a></li>
                    <li><a
                        className={currentBusinessOption.business_business_option_status == 'irrelevant' ? 'active' : ''}
                        href="" onClick={(e) => this.onClickUpdateStatus(e, 'irrelevant')}>Doesn't apply to me</a></li>
                </ul>
            </div>

        )

    }
}

SocialMediaRegistration.propTypes = {
    getBusinessCategories: PropTypes.func.isRequired,
    setBusinessCategoryId: PropTypes.func.isRequired,
    setSellGoods: PropTypes.func.isRequired,
    setCurrentTipCategory: PropTypes.func.isRequired,
    setCurrentBusinessOption: PropTypes.func.isRequired,
    onClickNext: PropTypes.func.isRequired,
    getBusinessOptionFromUrl: PropTypes.func.isRequired,
    saveBusinessOptionFormRequest: PropTypes.func.isRequired,
    getAppStatus: PropTypes.func.isRequired,
    addFlashMessage: PropTypes.func.isRequired
};

function mapStateToProps(state) {
    return {
        appStatus: state.appStatusReducer,
        auth: state.authReducer
    }
}

export default withRouter(
    connect(
        mapStateToProps,
        {
            getBusinessCategories,
            setBusinessCategoryId,
            setSellGoods,
            setCurrentTipCategory,
            setCurrentBusinessOption,
            getBusinessOptionFromUrl,
            saveBusinessOptionFormRequest,
            getAppStatus,
            addFlashMessage
        }
    )(SocialMediaRegistration))


